<h2>Hello<a class="header-anchor" id="Hello" href="#Hello">#</a></h2>
<p>这里是我经常撰写有关自己或他人的事迹与感受、对事物的看法和评价、一些日常工作中可能遇到的技术性话题讨论以及一些业余的教程的地方，感谢您的关注！本博客的所有内容，除了引用他人的语言或图片等媒体，以及有特殊声明外，均以 MIT 协议开放。</p>
<p>我个人从 2016 年开始撰写博客或者类似的内容，起初是 Wordpress，后来觉得太臃肿就随大流更换到了 Typecho。经历了一次或多次的主动或被动「删库」以后，我懂得了「<strong>无论博客的内容有多么的冷门、小众或者业余，挑选一个稳定的环境是最有必要做的事情</strong>」。后来我又了解到，「博客」实质上是「写给自己看的」。如果有人关注我、给我写评论、为我叫好，我会非常开心——只不过对目前而言为时过早，我并不对这些具有信心，也不奢求能够得到他人的赞许或者批评。因此，本博客在短时间内并不会开放评论，基本上可以算是写给自己看了...</p>
<p>无论如何，您依然可以通过的联系方式找到我，我一般周末在线。<router-link to="/Contact">这里</router-link></p>
<h2>World<a class="header-anchor" id="World" href="#World">#</a></h2>
<p>本博客的所有内容和基本网页已经放置在了 GitHub 上，仓库地址为 <a href="https://github.com/Subilan/Blog" class="external-link" target="_blank" rel="noopener noreferrer">https://github.com/Subilan/Blog<span class="external-link-icon mdi mdi-launch"></span></a>。<s>但是我并不会使用 GitHub Pages，反而会使用更稳定（价格也更昂贵）的阿里云 ECS。</s> <strong>光速打脸！</strong> 2019/8/31 本博客正式迁移至 GitHub Pages。同时，我也将本博客的域名 <code>subilan.win</code> 续费至了公元 2024 年，这一切只为了能够提供给各位良好的阅读体验，和避免自己的心血被第三方干涉掉。</p>
<blockquote>
<p>数据无价，做好备份是必要的。在先前的某次事故后，我领略到了这一点。不过，如果你要问我为什么没有备份，那是因为碰巧备份已经满了——我的所有资料均备份在同一个地方。于是被清除掉了，不久之后就发生了原博客被删库的不幸事件。</p>
